<template>
  <div class="account-info__item">
    <h4 class="account-info__item--title">{{ title }}</h4>
    <p class="account-info__item--value" :style="{color: colorText}" :title="value">{{type === 'price' ? '$' : null}}{{ valueFilter }}</p>
  </div>
</template>

<script lang="js">
import { simpleNumber } from "../../filters/valueFilter";
import { color } from "../../filters/colorText";

export default {
  name: 'AccountInfoItem',
  props: {
    title: String,
    value: Number,
    color: {
      type: String,
      default: '#00004F'
    },
    type: String
  },
  computed: {
    valueFilter() {
      return simpleNumber(this.value, 1)
    },

    colorText() {
      return color(this.value)
    }
  },
}
</script>

