export function simpleNumber(costOfIt, digits) {
    const lookup = [
        { value: 1, symbol: "" },
        { value: 1e3, symbol: "k" },
        { value: 1e6, symbol: "M" },
    ];
    const rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
    let item = lookup.slice().reverse().find(function(item) {
        return costOfIt >= item.value;
    });
    return item ? (costOfIt / item.value).toFixed(digits).replace(rx, "$1") + item.symbol : "0";
}