    mutations: {
        archivateTeaser(state, teaserID) {
            const updatedState = state.teasers.map(teaser => {
                if (teaser.id === teaserID) {
                    return Object.assign({}, teaser, teaser.isArchivated = true);
                }
                return  teaser
            })
            state.teasers = updatedState.slice();
        },

    },
    actions: {

    },
    getters: {
        allTeasers(state) {
            return state.teasers
        },
        archivatedTeasers(state) {
            return state.teasers.filter(teaser => teaser.isArchivated)
        },
        activeTeasers(state) {
            return state.teasers.filter(teaser => teaser.isArchivated === false)
        },
        activeTeasersLength(state) {
            const activeTeasers = state.teasers.filter(teaser => teaser.isArchivated === false)
            return activeTeasers.length
        }
    },
