<template>
  <div class="account__info--graphic account-info__item graphic">
    <p class="graphic__tag item-title">{{ tag }}</p>
    <h3 class="graphic__title">{{ title }}</h3>
    <p class="graphic__text">{{ text }}</p>
    <div class="diagram diagram-news">
      <svg viewBox="0 0 42 42">
        <circle class="diagram__center" cx="21" cy="21" r="15.91549430918954" fill="#fff"/>
        <circle class="diagram__ring" cx="21" cy="21" r="15.91549430918954" fill="transparent" stroke="#d2d3d4" stroke-width="5"></circle>
        <circle class="diagram__segment" cx="21" cy="21" r="15.91549430918954" fill="transparent" stroke-width="5"
                v-for="(part, i) of parts" :key="i"
                :stroke-dasharray="part.percent + ' ' + (100 - part.percent)" :stroke-dashoffset="this.offsets[i]"
                :stroke="part.color"
        ></circle>
      </svg>
      <p class="diagram__text">
        {{ name }}
      </p>
    </div>
    <ul class="diagram__list">
      <li class="diagram__list--item" v-for="(part, i) in parts" :key="i">
        <div class="dot" :style="{backgroundColor: part.color}"></div>
        <p class="name">{{ part.name }}</p>
        <p class="value">{{ part.percent }}%</p>
      </li>
    </ul>
  </div>
</template>

<script>

export default  {
  name: 'AccountGraphic',
  props: {
    tag: String,
    title: String,
    text: String,
    name: String,
    parts: Array
  },
  data: () => ({
    offset: 25,
    offsets: []
  }),
  mounted() {
    let part = this.parts;

    part.forEach((part, i) => {
      if (i === 0) {
        this.offsets.push(this.offset)
      } else if (i === 1) {
        this.offsets.push(100 - this.parts[i - 1].percent + this.offset)
      } else {
        this.offsets.push(this.offsets[i - 1] + (100 - this.parts[i - 1].percent))
      }
    })
  }
}
</script>

