export function color(value) {
    let color = 'green';
    if (value > 0 && value <= 1000) {
        color = '#FF5252'
    } else if (value > 1000 && value <= 24999) {
        color = '#66C357'
    } else if (value >= 25000 && value <= 34999) {
        color = '#B870F0'
    } else if (value >= 35000 && value <= 74999) {
        color = '#6D55FF'
    } else if (value >= 75000 && value <= 99999) {
        color = '#5378FB'
    } else if (value >= 100000 && value <= 999999) {
        color = '#00004F'
    } else if (value >= 1000000) {
        color = '#FFA800'
    } else {
        color = '#00004F'
    }

    return color
}