<template>
  <aside class="sidebar" :class="{'active': !sidebarOpened}">
    <div class="sidebar__toggler" @click="sidebarOpened = !sidebarOpened">
      <span></span>
      <span></span>
      <span></span>
    </div>
    <SidebarLink name="dashboard" link-name="Dashboard" href="/"/>
    <h3 class="sidebar__title">МОЙ КАБИНЕТ</h3>
    <ul class="sidebar__list">
      <li class="sidebar__list--item" v-for="(nav, i) in navigation" :key="i">
        <SidebarLink v-bind:link-name="nav.linkName" v-bind:href="nav.href" :name="nav.name" />
      </li>
    </ul>
    <h3 class="sidebar__title">ДРУГОЕ</h3>
    <SidebarLink name="faq" link-name="FAQ" href="faq"/>
  </aside>
</template>

<script lang="js">
import SidebarLink from "../components/SidebarLink";

export default {
  name: 'Sidebar',
  props: ["navigation"],
  components: {
    SidebarLink
  },
  data() {
    return {
      sidebarOpened: true
    }
  },
  watch: {
    sidebarOpened: function () {
      if (!this.sidebarOpened) {
        document.body.classList.add('sidebar-active')
      } else {
        document.body.classList.remove('sidebar-active')
      }
    }
  },
  mounted() {

  }
}

</script>

